@use 'https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&family=Geologica:wght@100;200;300;400;500;600;700;800;900&family=Open+Sans:wght@400;600;800&display=swap';

* {
    // margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-style: Geologica;
    transition: all 0.5s ease-in-out;
}

.container {
    width: 1208px;
    margin: 0 auto;
}

.redline-button {
    width: 168px;
    height: 40px;
    flex-shrink: 0;
    cursor: pointer;
    background: none;
    border: 4px solid #D42E12;
    color: #FFF;
    text-align: center;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-top: 15px;

    &:hover {
        border: 4px solid #FFF;
        color: #D42E12;
    }

    &:active {
        background: #D42E12;
        border: 4px solid #FFF;
        color: #FFF;
        transition: all 0.1s ease-in-out;

    }
}

.red-line {
    display: flex;
    margin: 0 auto;
    background: #C53720;
    width: 856px;
    height: 4px;
    flex-shrink: 0;
}